.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-image: url(../images/icons/icon-burger-menu.svg);
  background-size: cover;
  transition: all $transitionTime;

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  @include hover(transform, scale(1.2));

  &__contacts {
    position: relative;

    @include onDesktop {
      &:hover .icon__pnone-numbers {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &__pnone-numbers {
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align: end;
    width: 201px;
    gap: 8px;
    top: -30px;
    right: 32px;
    padding-right: 16px;
    opacity: 0;
    pointer-events: none;
  }

  &__phone {
    background-image: url(../images/icons/icon-phone-call.svg);
  }


  &__menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &__close {
    background-image: url(../images/icons/icon-close.svg);
  }
}
