.contacts {
  &__content {
    @include pageGrid;
  }

  &__wrapper {
    display: grid;
    row-gap: $main-row-gap;
    grid-column: 1/-1;

    margin-bottom: 48px;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-auto-flow: column;
      column-gap: 48px;
      grid-column: span 6;
    }
  }

  &__form {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__title {
    font-size: 16px;
  }

  &__phone-title {
    margin-block: 16px 4px;
    font-size: 14px;
  }

  &__address-title {
    margin-block: 8px 4px;
    font-size: 14px;
  }

  &__phone-and-address {
    font-weight: 700;
    text-decoration: none;
    line-height: 27px;
    color: $main-text-color;

    &--smaller-line-hight {
      line-height: 22.4px;
    }

    @include linkHoverEffects;
  }
}
