.buttons {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 56px;
  width: 100%;
  background-color: $main-background-color;
  border: none;

  font-family: inherit;
  font-size: 16px;
  line-height: 27px;
  font-weight: 700;
  text-decoration: none;
  color: $secondary-text-color;
  transition: $transitionTime;

  &:hover {
    background-color: #ae1c23;
  }

  &:active {
    background-color: #eb343d;
  }
}
