.now-on-view {
  &__title {
    margin-bottom: 48px;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
  }

  &__wrapper {
    display: grid;
    row-gap: $main-row-gap;
    margin-bottom: 40px;
  }

  &__button-wrapper {
    @include pageGrid;

  }

  &__button {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 2/6;
    }

    @include onDesktop {
      grid-column: 5/9;
    }
  }
}
