.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #d12d35;
  font-weight: 700;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: $main-background-color;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__bottom {
    padding-top: 40px;

    @include onDesktop{
      padding-top: 55px;
    }
  }

  &__nav {
    margin-bottom: 32px;

    @include onDesktop {
      margin-bottom: 48px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: fit-content;
  }

  &__call-to-order {
    padding-bottom: 7px;
    border-bottom: 1px solid $secondary-text-color;
    color: $secondary-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    padding-top: 8px;
  }
}
