.top-bar {
  padding-top: 25px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-top: 50px;
  }

  @include onDesktop {
    padding-top: 52px;
  }

  &__icons {
    display: flex;
    gap: 24px;
    align-items: center;

    @include onTablet {
      gap: 54px;
    }
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 46px;
    width: 64px;

    @include onTablet {
      height: 65px;
      width: 87px;
    }

    @include hover (transform, scale(1.2));
  }
}
