.exhibit {
  &__photo {
    margin: 0;
    width: 100%;
    margin-bottom: 24px;

    @include hover(transform, scale(1.05));

    @include onTablet {
      margin-bottom: 32px;
    }
  }

  &__title {
    text-align: center;
    font-size: 24px;
    line-height: 34px;
    font-weight: 700;

    color: $main-background-color;
  }

  &__description {
    font-family: Roboto, sans-serif;
    text-align: center;
  }
}
