.section__title {
  margin-bottom: 48px;
  font-size: 32px;
  line-height: 32px;
  font-weight: 700;
  text-align: center;
  letter-spacing: -1.5px;

  @include onTablet {
    font-size: 48px;
    line-height: 48px;
  }

  &--first-section {
    position: relative;
    margin-bottom: 32px;
    padding-top: 19px;
    text-align: left;

    &::before {
      content: "Plan your visit";
      font-size: 12px;
      line-height: 17px;
      color: $main-background-color;
      position: absolute;
      top: 1px;
      letter-spacing: 0;
    }
  }

  &--explore-online {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }
  }

  &--now-on-view {
    margin-bottom: 0;
  }
}
