.form {
  &__fields {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    margin-bottom: 32px;
    transition: all $transitionTime;
  }

  &__field {
    box-sizing: border-box;
    padding: 14px 16px;
    height: 48px;
    background-color: #f8f8fa;
    font-family: inherit;
    font-size: 14px;
    line-height: 20px;
    border: none;

    &:last-child {
      height: 144px;
      resize: none;
    }

    &::placeholder {
      color: #7e7e83;
    }

    &:hover {
      border: 1px solid #d5d7de;

      &::placeholder {
        color: $main-text-color;
      }
    }

    &:focus {
      border-color: $main-text-color;
      outline: none;
    }
  }
}
