.museum-hours {
  @include pageGrid;

  &__title {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 1/4;
    }

    @include onDesktop {
      grid-column: 1/6;
    }
  }

  &__content {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 4/7;
    }

    @include onDesktop {
      grid-column: 6/-1;
    }
  }

  &__workingTime-list {
    display: grid;
    row-gap: 8px;
    column-gap: 24px;
    grid-template-columns: 96px 1fr;
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    list-style: none;

    & :nth-child(odd) {
      font-weight: 700;
    }

    &--Cloisters {
      grid-template-columns: 121px 1fr;
    }
  }

  &__content-title {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 16px;
  }

  &__holidays {
    padding-bottom: 24px;

    &--Cloisters {
      padding-bottom: 0;
    }
  }
}
