.gallery {
  &__photos {
    @include pageGrid;
    margin-bottom: 24px;

    &--central-gallery {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-content: center;
      column-gap: 8px;

      @include onTablet {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 24px;

        & :nth-child(3) {
          display: none;
        }
      }

      @include onDesktop {
        grid-template-columns: repeat(3, 1fr);

        & :nth-child(3) {
          display: inline-block;
        }
      }
    }
  }

  &__link {
    position: relative;

    &--wide {
      grid-column: span 1;

      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;
    }

    &--not-central-square {
      grid-column: span 1;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right;

    @include hover(transform, scale(1.05));

    &--align-left {
      object-fit: cover;
      object-position: 30% 20%;
    }
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    color: $main-background-color;
  }
}
